
/*===============ADD VACATION ==============*/

.addVacationDiv{

    background:linear-gradient(rgba(255,255,255,2.5), rgba(255,255,255,0.8)), url("/images/admin/addVacation.jpg");

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    justify-content: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
     box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .8);   
    /* border-radius: 5px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;    */

}


.addVacationDiv:before {
    content: "";
    position: absolute;
    background: inherit;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    filter: blur(20px);
    margin: -20px;
    opacity: 0.5;
    min-width: 250px;


}   

.addVacationDiv>div{
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    justify-content: center;

}

.addVacationDiv>div>div{
    text-align: left;
    width: auto;
}

.addVacationDiv>div>div>label{
    font-size: 20px;
} 

.addVacationDiv>div>div>input{
    font-size: 20px;
    margin: 10px;
    text-align: left;
} 

.addVacationDiv> div> button{
    font-size: 25px;
}

.addVacationDiv> div> button :hover{
    text-decoration: none;
}


.errorMessage{
    margin-top: 5px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 15px;
    color: red;
}

#previewImg{
    width: 100%;
    max-width: 350px;
}

.subText{
    font-size: 15px;
}

.buttonSpace{
    margin-left: 10px;
    margin-bottom: 20px;
}
#cancelButton{
    color: white;

}
#saveButton{
    background-color: #04bfee !important;
}

#title{
    color: #04bfee ;
    font-family: 'Staatliches', cursive;

}
