/* =============ADMIN PAGE=============== */

/*TOP - BUTTONS */

#graphButton{
    background-color: white ;
    justify-content: center;
    width: 100%;
    font-size: 40px;
    border-color: lightgray;
    border-width: 0.3px;
    border-style: solid;
    color: black; 

}


#graphNavLinkButton :hover{
    transition: 0.5s;
    -webkit-transform: scale(0.98);
        -ms-transform: scale(0.98);
        transform: scale(0.98);

}


#addVacationCard, .grow, .grow:hover{
    
    border-radius: 10px 10px 0px 0px;
    margin-left: 10px;
    margin-top: 10px;
    
}


.grow { 
    transition: all .2s ease-in-out; 
    }
    
.grow:hover { 
transform: scale(1.03); 
}


.growGraph { 
    transition: all .2s ease-in-out; 
    }
    
.growGraph:hover { 
transform: scale(1.1); 
}


#plusSign{
    background-image: url("/images/admin/plusSignNew.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 1.0 !important;
    filter: blur(0px) !important;
    content: none;
    min-height: 100px;

}


/*======= VACATIONS LIST ELEMENTS========*/


#errorPage{
    background-image: url("/images/error/pageNotFound.png");
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: center;
    z-index: 99 !important;


}

#mainDiv{
    background-image: url("/images/admin/admin.jpg");
    background:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)), url("/images/admin/admin.jpg");

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed ;
    min-height: 100% !important;
    min-width: 280px;



}

.vacationsMainDiv{
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    justify-content: center;
    
    background-color: transparent;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

 .vacationsMainDiv > div{

    margin: 10px;
    border-width: 2px;
    width: 250px;
    border-color: transparent;
    border-style: solid;
    background-color: white;
    border-radius: 10px;
    text-align: center;
    justify-content: center;
    position: relative;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .8);   
    border-radius: 5px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;
    backdrop-filter: blur(5px);

}
.vacationsMainDiv > div:before {

    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    filter: blur(20px);
    margin: -20px;
    background-color: white;
    opacity: 0.3;
}


.vacationsMainDiv > div > h1{
    font-size: 30px;
    margin: 3px;
}

.vacationsMainDiv > div > h3{
    font-size: 20px;
    margin: 3px;
}

.vacationsMainDiv > div > h3 > span{ 
    font-weight: lighter;
    margin:3px;
}


.vacationImg{
    width: auto;
    max-width: 250px;
    height: 150px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;

}

.backgroundImg{
    background-image: url("/images/error/lostManNew.png");
    display: flex;
    width: auto;
    max-width: 250px;
    height: 150px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    justify-content: center;
}

#descriptionField{
    width: 100%;
    border-radius: 0px;
    border: transparent;
    background-color: transparent;
    padding-left: 10px;
    
    
}
#description{
    font-weight: lighter;
    margin-bottom: 10px;

}


/* BLOCK BACKGROUND WINDOW */

#blockBackgroundDiv{
    
    background-color: black;
    opacity: 0.5;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 98 !important;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
}
#saveButton{
    background-color: #04bfee !important;
}

#title{
    color: #04bfee;
    font-family: 'Staatliches', cursive;

}


/* WHEN SCREEN IS BETWEEN 380px And 560px  */

@media only screen and (max-width: 560px)and (min-width: 380px) {
    .vacationsMainDiv > div{
        margin: 5px;
        width: 170px;
    }

    .vacationsMainDiv > div > h1{
    font-size: 23px;
    margin:0px;
    }

    .vacationsMainDiv > div > h3{
    font-size: 15px;
    font-weight: lighter;
    margin:3px;

    }
    .vacationsMainDiv > div > h3 > span{ 
    font-size: 20px;
    font-weight: lighter;
    margin:3px;

    }
    #description{ 
    font-size: 13px;
    font-weight: 600;
    margin:3px;
    font-weight: lighter;


    }

    .vacationImg{
    width: auto;
    max-width: 250px;
    height: 150px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    }

  }

/* WHEN SCREEN IS Smaller Then 380px  */

@media only screen and (max-width: 380px) {
    .vacationsMainDiv > div{
        margin: 5px;
        width: 120px;
    }

    .vacationsMainDiv > div > h1{
    font-size: 23px;
    margin:0px;
    }

    .vacationsMainDiv > div > h3{
    font-size: 15px;
    font-weight: lighter;
    margin:3px;

    }
    .vacationsMainDiv > div > h3 > span{ 
    font-size: 15px;
    font-weight: bolder;
    margin:3px;

    }
    #description{ 
    font-size: 13px;
    font-weight: 600;
    margin:3px;
    font-weight: lighter;

    }

    .vacationImg{
    width: auto;
    width: 120px;
    height: 100px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    }

    .backgroundImg{
    width: 120px;
    height: 100px;
    }
}


/*==================== VACATION CARD BUTTONS - ADD/EDIT=============  */
.roundButtons{

    border-radius: 50%;
    height: 20px;
    border-color: transparent;
    background-color: transparent;
    margin-top: 15px;
}


#adminButtons :hover {
    color: #f8f9fa;
  }

#adminButtons :focus {
    background-color: transparent !important;
  }

#adminButtons{
    display: flex;
    flex-direction: column;
    position: absolute;
    font-size: 26px;
    top:0;
    right: 0;;
    color: lightgray;
    -webkit-text-stroke-width: 0.3px;
    -webkit-text-stroke-color: black;
}

/* EDIT VACATION WINDOW */

.editVacationDiv{
    text-align: center;
    justify-content: center;
    background-color: #f8f9fa;

    display: flex;
    flex-direction: column;
    position: fixed;

    max-height: 80%;

    padding-left: 20px;
    padding-right: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    border-radius: 20px;
    top: 10%;
    justify-content: center;
    border-radius: 20px;
    z-index: 99 !important;
    min-width: 260px;

    border-style: solid;
    border-color: black;
    border-width: 3px;
    background:linear-gradient(rgba(255,255,255,2.5), rgba(255,255,255,0.8)), url("/images/admin/addVacation.jpg");

}

@media only screen and (min-width: 600px) {
    .editVacationDiv{
        width: 80%;
    }
}
.editVacationDiv>div{
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    width: 100%;



}
.editVacationDiv>div>div{
    text-align: left;
    width: auto;
    height: 100%;
}
.addVacationDiv>div>div>label{
    font-size: 25px !important;

} 
.editVacationDiv>div>div>input{
    font-size: 20px;
    margin: 10px;
    text-align: left;
    
} 

.airplaneImg{
    display: inline-flex;
    width: 30px;
    height: 30px;
    padding-right: 6px;
}

.editVacationDiv> div> button{
    font-size: 25px;
}

.editVacationDiv> div> button :hover{
    text-decoration: none;
}

#editButtonsDiv{
    text-align: center;
    padding: 10px;
}

.buttonSpace{
    margin-left: 10px;
}

.errorMessage{ 
    margin-top: 5px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 15px;
    color: red;
    text-align: center;
}

.heartBackground{
    background-image: url("/images/admin/heart.webp");
    width: 40px;
    height: 40px;
    max-width: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    left: 0;
    top: 0;
}
