#graphBody{
    background-color: white;

}
.mainDiv{
    position: relative;
    top: 100px;
    /* height: 100% !important; */
    padding-left: 5px;
    padding-right: 5px;

    min-width: 250px;
    background-color: transparent;

}



/*TOP - BUTTONS */

#homeButton{
    background-color: white ;
    justify-content: center;
    width: 100%;
    font-size: 40px;
    border-color: lightgray;
    border-width: 0.3px;
    border-style: solid;
    color: black; 

}


#homeNavLinkButton :hover{
    transition: 0.5s;
    -webkit-transform: scale(0.98);
        -ms-transform: scale(0.98);
        transform: scale(0.98);

}


#addVacationCard, .grow, .grow:hover{
    
    border-radius: 10px 10px 0px 0px;
    margin-left: 10px;
    margin-top: 10px;
    
}


.grow { 
    transition: all .2s ease-in-out; 
    }
    
.grow:hover { 
transform: scale(1.03); 
}


.growGraph { 
    transition: all .2s ease-in-out; 
    }
    
.growGraph:hover { 
transform: scale(1.1); 
}


