

/*===============VACATIONS PAGE ==============*/
.vacationsMain {
    background-image: url("/images/vacations/family.jpg");

    background-attachment: fixed ;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    min-height: 100% !important;
    min-width: 280px;
}

.tables{

    display: flex;
    flex-wrap: wrap;
    background-color: transparent;
    justify-content: center;

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;


}

.tables > div{
    margin: 10px;
    border-width: 2px;
    width: 250px;
    border-color: transparent;
    border-style: solid;
    background-color: white;
    border-radius: 10px;
    text-align: center;
    justify-content: center;
    position: relative;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .8);   
    border-radius: 5px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;
    backdrop-filter: blur(5px);



}

.tables > div:before {
    content: "";
    position: absolute;
    background: inherit;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    filter: blur(20px);
    margin: -20px;
    background-color: white;
    opacity: 0.3;
}


.tables > div > h1{
    font-size: 30px;
    margin: 3px;
}

.description{
    margin-bottom: 10px !important;
}

.tables > div > h3{
    font-size: 20px;
    margin: 3px;
    font-weight: lighter;

}

.tables > div > h3 > span{ 
    font-weight: lighter;
    margin:3px;
}


.vacationImg{
    height: 150px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
}

.airplaneImg{
    display: inline-flex;
    width: 30px;
    height: 30px;
    padding-right: 6px;
}

.backgroundImg{
    background-image: url("/images/error/lostManNew.png");
    width: auto;
    max-width: 250px;
    height: 150px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.heartBackground{
    background-image: url("/images/admin/heart.webp");
    width: 50px;
    height: 50px;
    max-width: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position:0px -4px;
    left: 50%;
    position: absolute;
    left: 0;
    top: 0;
}





@media only screen and (max-width: 560px)and (min-width: 380px) {
    .tables > div{
        margin: 5px;
        width: 170px;
    }

    .tables > div > h1{
    font-size: 23px;
    margin:0px;
    }

    .tables > div > h3{
    font-size: 15px;
    font-weight: lighter;
    margin:3px;

    }
    .tables > div > h3 > span{ 
    font-size: 20px;
    font-weight: lighter;
    margin:3px;

    }
    .description{ 
    font-size: 13px;
    font-weight: 600;
    margin:3px;

    }

    .vacationImg{
    width: auto;
    max-width: 250px;
    height: 150px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    }

  }


@media only screen and (max-width: 380px) {
    .tables > div{
        margin: 5px;
        width: 120px;
    }

    .tables > div > h1{
    font-size: 23px;
    margin:0px;
    }

    .tables > div > h3{
    font-size: 15px;
    font-weight: lighter;
    margin:3px;

    }
    .tables > div > h3 > span{ 
    font-size: 15px;
    font-weight: bolder;
    margin:3px;

    }
    .description{ 
    font-size: 13px;
    font-weight: 600;
    margin:3px;

    }

    .vacationImg{
    width: auto;
    width: 120px;
    height: 100px;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    }

    .backgroundImg{
    width: 120px;
    height: 100px;
    }
}




