.layout {

    display:  flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    


}

.layout>main {
    height: 100%; 
    text-align: center;
    align-items: center;
    background-blend-mode: overlay;
    background-attachment: fixed ;

    
    padding-top: 100px;
} 

@media only screen and (max-width: 600px)and (min-width: 460px) {
    .layout>main {
        padding-top: 66px;   
    }
 }

 @media only screen and (max-width: 460px) {
    .layout>main {
        padding-top: 50px;   
    }
 }


