/* =============HEADER=============== */


.header {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-areas:
    'left right'; 
    min-width: 280px;


    position: fixed;
    width: 100%;
    background-image: url('/images/header/header.png');
    background-position: center;
    background-size: cover;
    background-blend-mode: overlay;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-style: initial;
    z-index: 99 !important;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}


/* =============HEADER ELEMENTS=============== */

#left{
    grid-area: left;
    display: flex;
    width: 100%;
}

#right{
    display: flex;
    flex-direction: row;
    grid-area: right;
    align-self: right;
    justify-content: right;
    width: 100%;
    align-items: right;
    text-align: right;
}

#logo{
    justify-content: center;
    text-align: center;
    width: 250px;
    margin: 10px;
}

.login{
    justify-content: right;
    text-align: right;
    margin-right: 40px;
    font-size: 25px;
    font-weight: lighter;
}
#loginSpan{
    justify-content: right;
    text-align: right;
    margin-right: 40px;
    font-size: 25px;
    font-weight: lighter;
    position: absolute;
    bottom: 10px;
    right: 0;}

#name{
    justify-content: right;
    text-align: right;
    margin-right: 20px;
    font-size: 25px;
    font-weight: lighter;
}


#loginDiv{
    justify-content: right;
    text-align: right;
    position: absolute;
    bottom: 10px;
    right: 0;

}


@media only screen and (max-width: 460px) {
    #name{
        font-size: 15px;
        margin-right: 10px;

    }
    .login{
        font-size: 15px;
        margin-right: 3px;
    }

    #logo{
    justify-content: center;
    text-align: center;
    width: 100px;
    margin: 10px;
    }
    
  }

@media only screen and (max-width: 600px)and (min-width: 460px) {
    #logo{
        justify-content: center;
        text-align: center;
        width: 150px;
        margin: 10px;
    }
    .login{
        margin-right: 5px;
    }
    #name{
        margin-right: 15px;
    }
    
  }


#logoButton{
    background-color: transparent;
    border: transparent;
}


