/* =============REGISTER FORM=============== */


.registerDiv{
    text-align: center;
    justify-content: center;
    background-color: white;
    font-weight: 300;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .8);   
    border-radius: 5px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 250px;
    margin-top: 30px;


}


.registerDiv:before {
    content: "";
    position: absolute;
    background: inherit;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    filter: blur(20px);
    margin: -20px;
    background-color: white;
    opacity: 0.2;
}

.registerDiv>h1{
    font-weight: 800;
    font-weight: lighter;
    font-size: 50px;
    font-family: 'Staatliches', cursive;
}

.registerDiv>div{
    display: flex;
    justify-content: center;

}
.registerDiv>div>div{
    text-align: left;
    width: auto;
}
.registerDiv>div>div>label{
    font-size: 20px;
    font-weight: 200;


} 
.registerDiv>div>div>input{
    font-size: 20px;
    font-size: 30px;
    font-weight: lighter;
    margin: 10px;
    text-align: left;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    
} 


.registerDiv>button{
    font-size: 25px;
    width: 270px;
    font-weight: lighter;
    background-color: #04bfee !important;
}
@media only screen and (max-width: 300px) {
    .registerDiv>button{
        width: 90%;
    }
 }


.registerDiv>h3{
    font-weight: 800;
    font-size: 30px;
    font-family: 'Staatliches', cursive;
    font-weight: lighter;


}


.errorMessage{
    margin-top: 5px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 15px;
    color: black;

    
}